@import './var.scss';
@import './mixins';
@import './theme';

.hero-intro-pump-card {
  width: 600px;
  margin-left: 40px;
  &-banner {
    cursor: pointer;
    img {
      display: block;
      width: 100%;
    }
  }

  &-sun-intro-card {
    margin-top: 45px;
  }
  &-title {
    margin-bottom: 19px;
    color: $color-text-default;
    font-weight: 800;
    font-size: 40px;
    line-height: 44px;
    background: linear-gradient(180deg, $color-text-default 0%, #c5aaff 100%);
    background-clip: text;
    color: transparent;
    @include lightThemeOnly {
      background: linear-gradient(180deg, #bfa0ff 0%, #5100ff 100%);
      background-clip: text;
    }
  }
  &-content {
    margin-bottom: 16px;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: $color-text-default;
    opacity: 0.7;
    font-family: PingFangSC, PingFang SC;
  }
  &-main {
    position: relative;
    display: flex;
    &-l {
      width: 222px;
      margin-right: 22px;
      .hero-intro-pump-card-content {
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    &-r {
      position: relative;
      flex: 1;
      width: 0;
      img {
        position: absolute;
        left: 0;
        top: -9px;
        display: block;
        width: 438px;
      }
    }
  }
}

@include mobile-only {
  .hero-intro-pump-card {
    display: none;
  }
}
