@import '../../assets/css/mixins';
@import '../../assets/css/theme';

.dropdown_wrapper {
  background: #36254e;
  padding: 0 12px;
  border-radius: 6px;
  cursor: pointer;
}
.dropdown_choose {
  background: linear-gradient(93.51deg, #6577ff 0%, #6400ff 99.21%);
}
.dropdown {
  display: inline-flex;
  overflow: hidden;
  align-items: center;
  font-size: 14px;
  height: 40px;
  padding-right: 25px;

  img {
    width: 26px;
    height: 26px;
    vertical-align: middle;
    border-radius: 50%;
    overflow: hidden;
  }
  @include darkThemeOnly {
    background: url(../../assets/images/arr.svg) right no-repeat;
  }
  &_choose {
    border-radius: 5px;
    color: var(--color-button-primary-text);
    padding: 4px 10px 4px 10px;
    font-size: 14px;
    height: 36px;
    display: flex;
    align-items: center;
    background-color: var(--color-button-primary);
  }
  &_disabled {
    background: none !important;
    cursor: auto !important;
  }
  &_symbol {
    margin: 0 0 0 5px;
    font-family: Poppins;
    font-weight: 600;
    font-size: 16px;
    color: #fff;
    vertical-align: middle;
    display: inline-block;
    max-width: 100px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
