@import '../../../assets/css/theme';

.setting_popover.ant-popover {
  width: 280px;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  .ant-popover-inner {
    background: rgba(38, 25, 61, 1);
  }
  .ant-popover-arrow {
    opacity: 0;
  }
}
#root .new_setting_icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  text-align: center;
  line-height: 22px;
  background: rgba(54, 37, 78, 1);
  cursor: pointer;
  &:hover {
    background-color: rgba(68, 53, 100, 1);
    img {
      opacity: 1;
    }
  }
  img {
    display: block;
    width: 20px;
    height: 20px;
    opacity: 0.7;
  }
}
