@import './var.scss';
@import './mixins';
@import './theme';

.home-base-banner {
  position: relative;
  img {
    display: block;
    width: 100%;
    height: 420px;
    @include mobile-only {
      height: 160px;
    }
  }
  .home-base-banner-item {
    .home-base-banner-img-dark {
      display: block !important;
      @include lightThemeOnly {
        display: none !important;
      }
    }
    .home-base-banner-img-light {
      display: none !important;
      @include lightThemeOnly {
        display: block !important;
      }
    }
  }

  .home-base-banner-switch-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 40px;
    height: 40px;
    border-radius: 40px;
    background: rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(255, 255, 255, 0.5);
    cursor: pointer;
    opacity: 0.5;
    color: #fff;
    font-size: 12px;
    &:hover {
      opacity: 1;
    }
    img {
      display: block;
    }
    @include mobile-only {
      display: none;
    }
  }
  .home-base-banner-prev {
    left: 20px;
  }
  .home-base-banner-next {
    right: 20px;
  }
}

.home-banner-img {
  position: relative;
}

.home-banner-btn {
  display: flex;
  position: absolute;
  bottom: 30px;
  right: 30px;
  @include mobile-only {
    bottom: 12px;
    right: 10px;
  }
  > button {
    height: 45px;
    border-radius: 33px;
    padding: 0px 20px;
    font-family: Helvetica Neue;
    font-size: 24px;
    font-weight: 700;
    line-height: 30px;
    cursor: pointer;
    @include mobile-only {
      height: 19px;
      border-radius: 15px;
      padding: 0px 8px;
      font-size: 12px;
      line-height: 15px;
    }

    &:nth-child(1) {
      border: 2px solid #ffffff4d;
      background: #7a3cff;
      color: #fff;
      margin-right: 30px;
      @include mobile-only {
        margin-right: 10px;
        border: 1px solid #ffffff4d;
      }
    }
    &:nth-child(2) {
      border: 2px solid #7a3cff;
      color: #7a3cff;
      @include mobile-only {
        border: 1px solid #7a3cff;
      }
    }
  }
}

.home-base-banner .ant-carousel .slick-dots {
  bottom: 20px;
  @include mobile-only {
    bottom: 10px;
  }
  li {
    width: 20px;
    height: 12px;
    border-radius: 6px;
    background-color: rgba(#ffffff, 0.56);
    margin-right: 10px;
    box-shadow: 0px 0px 20px 0px rgba(#000, 0.3);
    @include mobile-only {
      width: 10px;
      height: 4px;
      margin-right: 4px;
    }
    button {
      border-radius: 6px;
      background: transparent !important;
    }
  }
  li.slick-active {
    width: 50px;
    height: 12px;
    @include mobile-only {
      width: 16px;
      height: 4px;
    }
    background-color: #8c51ff;
    @include lightThemeOnly {
      background-color: #5a14e1;
    }
  }
}
