@import './var.scss';
@import './theme';
@import './mixins';

.tr-tooltip {
  .ant-tooltip-inner {
    width: fit-content;
    padding: 0;
    border-radius: 20px;
    overflow: hidden;
    .tr-content li {
      width: 450px;
    }
  }
}

.trade-router-modal.ant-modal {
  .ant-modal-close {
    display: none;
  }
  .ant-modal-content {
    background: #403858;
    padding: 0;
    border-radius: 20px;
    overflow: hidden;
  }

  .ant-modal-title {
    text-align: left !important;
    font-size: 15px !important;
  }
  .ant-modal-close {
    display: none;
  }
}

.trade-router {
  margin: 20px 0;
  font-size: 14px;
  color: #fff;
  line-height: 20px;
  cursor: pointer;
  @include mobile-only {
    text-align: right;
  }
  .renderRoutrOuter {
    display: flex;
    .renderRoutrOuter-r {
      flex: 1;
    }
  }
  .trade-router-txt {
    margin-right: 10px;
    font-size: 14px;
    font-family: Poppins;
    font-weight: 400;
    color: #a7a2c9;
    white-space: nowrap;
  }
  .tr-arrow {
    display: inline-block;
    width: 8px;
    height: 8px;
    background: url(../images/homeTokens/ico_router_path2.png) 0 0 no-repeat;
    background-size: 100% 100%;
    margin: 0 8px;
  }

  img {
    position: relative;
    width: 15px;
    margin-right: 6px;
    vertical-align: middle;
    top: -1px;
    border-radius: 50%;
    border: 1px solid #ddd;
    background: #fff;

    @include mobile-only {
      width: 16px;
    }
  }
}

.tr-content {
  padding: 10px;
  background: rgba(38, 25, 61, 1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  .token {
    margin-right: 4px;
  }

  > p {
    display: flex;
    align-items: center;
    font-family: Avenir Next;
    font-size: 14px;
    line-height: 19px;
    color: #fff;
    margin-bottom: 16px;

    > img {
      width: 16px;
      height: 16px;
      margin-right: 4px;
    }
  }

  > div {
    min-width: 160px;
  }

  ul {
    margin: 0;
    padding: 0;
  }

  li {
    list-style: none;
    margin-bottom: 10px;
    padding: 12px 20px 0;
    position: relative;
    background: rgba(51, 37, 78, 1);
    border: 1px solid transparent;
    border-radius: 6px;
    &:last-child {
      margin-bottom: 0;
    }
    .tr-arrow {
      display: none;
      width: 8px;
      height: 8px;
      background: url(../images/homeTokens/arrow_default.png) 0 0 no-repeat;
      background-size: 100% 100%;
    }
    &.active {
      background: rgba(68, 53, 100, 1);
      border-color: rgba(219, 202, 255, 0.3);
      // &::after {
      //   content: '';
      //   border: none;
      //   background: url(../images/homeTokens/router-path-checked.png) no-repeat;
      //   background-size: 100% 100%;
      // }
      .tr-content-li-top {
        .tr-content-li-title {
          color: #fff;
        }
      }
    }
    &:hover {
      background: rgba(68, 53, 100, 1);
      .tr-content-li-top {
        .tr-content-li-title {
          color: #fff;
        }
      }
    }
    &:last-child {
      .tr-content-li-down {
        border-bottom: none;
      }
    }

    .tr-content-li-top {
      display: flex;
      align-items: center;
      .tr-content-li-title {
        margin-right: 10px;
        font-family: Poppins;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        white-space: nowrap;
        color: rgba(167, 162, 201, 1);
      }
      .tr-content-li-top-r {
        flex: 1;
        width: 0;
        font-family: Poppins;
        font-weight: 400;
        font-size: 12px;
        line-height: 24px;
        color: rgba(167, 162, 201, 1);
        word-break: break-all;
      }
    }
    .tr-content-li-top-r-num {
      margin: 0 4px;
    }
    .tr-content-li-down {
      display: flex;
      flex-flow: wrap;
      font-size: 12px;
      font-weight: 400;
      margin-top: 12px;
      padding-top: 12px;
      border-top: 1px solid rgba(219, 202, 255, 0.1);

      .tr-content-li-pair {
        display: flex;
        align-items: center;
        margin-right: 8px;
        margin-bottom: 10px;
        .tr-content-li-pair-op {
          display: flex;
          align-items: center;
          font-size: 12px;
          font-family: Poppins;
          font-weight: 400;
          color: #fff;
        }
        img {
          width: 16px;
          height: 16px;
          margin-right: 4px;
          border-radius: 50%;
          border: 1px solid #fff;
          background: #fff;
        }
        &:last-child {
          margin-right: 0;

          .tr-arrow {
            display: none;
          }
        }
        .path-pool {
          align-items: center;
        }
        .tr-pool {
          margin: 0 6px;
          background: rgba(255, 255, 255, 0.1);
          color: #fff-5;
          padding: 0 4px;
          border-radius: 2px;
          font-size: 11px;
          line-height: 18px;
        }
      }
    }
  }
}
